body {
  justify-content: center;
  align-self: center;
  align-items: center;
  margin: 0;
  font-family: Roboto, sans-serif;
}

.header {
  box-sizing: border-box;
  flex-direction: row;
  justify-content: space-between;
  width: 100vw;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.photo-logo {
  margin-top: 10px;
}

.nav {
  height: 60px;
}

.nav__list {
  margin-top: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  margin-right: 20px;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__link {
  color: #060b35;
  text-transform: uppercase;
  text-align: center;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: flex;
}

.nav__link:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
}

.container {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 48px 46px;
  padding: 40px 50px;
  display: grid;
}

@media (width >= 488px) {
  .container {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .container {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .container {
    grid-template-columns: repeat(4, 200px);
  }
}

